@charset "UTF-8";
html, body, div, span, applet, object, iframe,
h1, h2, h3, .kicker, .gform_wrapper .gform_heading .gform_description, .single-article .entry-content p.lead, .featured-story h2, .single-article .widget-area h2, h4, h5, dt, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, .entry-content ol, .wp-editor ol, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

body {
  line-height: 1;
}

ol, ul, .entry-content ol, .wp-editor ol {
  list-style: none;
}

blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, *:after, *:before {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.cf:before, .main-menu-wrap:before, .aux-menu-wrap:before, .breadcrumb-wrap:before, .gal-with-caption:before, .academic-list:before, .content-box-module--tweet .content-box-img:before,
.cf:after,
.main-menu-wrap:after,
.aux-menu-wrap:after,
.breadcrumb-wrap:after,
.gal-with-caption:after,
.academic-list:after,
.content-box-module--tweet .content-box-img:after {
  content: "";
  display: table;
}

.cf:after, .main-menu-wrap:after, .aux-menu-wrap:after, .breadcrumb-wrap:after, .gal-with-caption:after, .academic-list:after, .content-box-module--tweet .content-box-img:after {
  clear: both;
}

.cf, .main-menu-wrap, .aux-menu-wrap, .breadcrumb-wrap, .gal-with-caption, .academic-list, .content-box-module--tweet .content-box-img {
  *zoom: 1;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}
.container:before, .container:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}
.container:after {
  clear: both;
}

.row {
  margin-left: -15px;
  margin-right: -15px;
}
.row:before, .row:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}
.row:after {
  clear: both;
}

